import React from 'react';
import {format} from 'date-fns';

export default function App(props) {

    const padTo2Digits = (num) => {
        return num.toString().padStart(2, '0');
    };

    const convertToMinutesHours = (milliseconds) => {
        let seconds = Math.floor(milliseconds / 1000);
        let minutes = Math.floor(seconds / 60);
        let hours = Math.floor(minutes / 60);

        seconds = seconds % 60;
        minutes = minutes % 60;

        hours = hours % 24;
        return `${padTo2Digits(hours)}:${padTo2Digits(minutes)}:${padTo2Digits(
            seconds,
        )}`;
    };

    return (
        <div className="ina-debugger-section">
            <ul>
                <li className="ina-timeout-remaining-debugger">{props.remaining ? <><strong>{inactiveLogout.settings.debug_msg.logout} </strong> {convertToMinutesHours(props.remaining - props.promptTimeout)}</> : <>{inactiveLogout.settings.debug_msg.active}</>}</li>
                <li className="ina-timeout-active-debugger">
                    <strong>{inactiveLogout.settings.debug_msg.last_active} </strong> {format(props.lastActive, 'MM-dd-yyyy HH:MM:ss')}
                </li>
            </ul>
        </div>
    );
}
