import React, {useState, useEffect} from 'react';
import {useIdleTimer} from 'react-idle-timer';
import Modal from 'react-modal';
import ModalStyles from './Styles/ModalStyle';
import Debugger from "./components/Debugger";

export default function App() {
    // Set timeout values
    const timeout = 1000 * inactiveLogout.settings.timeout;

    //Counter for the popup
    const promptTimeout = inactiveLogout.settings.disable_countdown ? 1 : inactiveLogout.settings.countdown_timeout && inactiveLogout.settings.countdown_timeout === 0 ? 1 : 1000 * inactiveLogout.settings.countdown_timeout;

    // Modal open state
    const [modal, setModal] = useState(false);

    //Get Last Active Timestamp
    const [lastActive, setLastActive] = useState(+new Date());

    // Time before idle
    const [remaining, setRemaining] = useState(0);
    const [logoutMsg, setLogoutMsg] = useState(false);

    const onPrompt = () => {
        setModal(true);
        setRemaining(promptTimeout);
        setLogoutMsg(false);
    };

    const handleOnIdle = () => {
        pause();
        setRemaining(0);

        if (!inactiveLogout.settings.warn_message_enabled) {
            fetch(inactiveLogout.ajaxurl, {
                method: 'POST',
                credentials: 'same-origin',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                    'Cache-Control': 'no-cache',
                },
                body: 'action=ina_logout_session&security=' + inactiveLogout.security,
            })
                .then((resp) => resp.json())
                .then((data) => {
                    if (data.redirect_url) {
                        window.location.href = data.redirect_url;
                        return;
                    }

                    setLogoutMsg(data.msg);
                });
        }
    };

    const handleOnActive = () => {
        setModal(false);
        setRemaining(0);
    };

    const {
        getRemainingTime,
        pause,
        isPrompted,
        activate,
        getLastActiveTime
    } = useIdleTimer({
        timeout,
        promptTimeout,
        onPrompt,
        onIdle: handleOnIdle,
        onActive: handleOnActive,
        crossTab: true,
        syncTimers: true,
        name: 'inactive-logout-timer',
    });

    const handleStillHere = () => {
        setModal(false);
        activate();
    };

    const reloadPage = () => {
        setModal(false);
        window.location.reload();
    };

    useEffect(() => {
        const interval = setInterval(() => {

            if (inactiveLogout.settings.debug_js) {
                setRemaining(Math.ceil(getRemainingTime()));
                setLastActive(getLastActiveTime());
            }

            if (isPrompted()) {
                setRemaining(Math.ceil(getRemainingTime() / 1000));
            }
        }, 1000);

        return () => {
            clearInterval(interval);
        };
    }, [getRemainingTime, isPrompted]);

    Modal.setAppElement('#ina-logout-modal-container');

    return (
        <>
            {inactiveLogout.settings.debug_js && (
                <Debugger
                    timeout={timeout}
                    remaining={remaining}
                    lastActive={lastActive}
                    promptTimeout={promptTimeout}
                />
            )}

            <Modal
                isOpen={modal}
                style={ModalStyles}
            >
                <div className="ina-modal-inside">
                    <h2>{inactiveLogout.modal.headerText}</h2>
                    <div className="ina-modal-content" dangerouslySetInnerHTML={{__html: logoutMsg ? logoutMsg : inactiveLogout.modal.message}}/>
                    <div className="ina-modal-actions">
                        {logoutMsg ? <>
                            <button onClick={reloadPage} className="ina-button ina-stay-logged-in">{inactiveLogout.modal.ok}</button>
                            <button onClick={handleStillHere} className="ina-button ina-close-without-reloading">{inactiveLogout.modal.close}</button>
                        </> : <button onClick={handleStillHere} className="ina-button ina-stay-logged-in">{inactiveLogout.modal.continue} {!inactiveLogout.settings.warn_message_enabled && remaining > 0 ? '(' + remaining + ')' : ''}</button>}
                    </div>
                </div>
            </Modal>
        </>
    );
}
